footer{
    background:var(--color-bg-variant);
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
}

footer a{
    color:var(--color-white);
}

.footer__logo{
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
}

.permalinks{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin:  auto 4 rem;  
    margin-bottom: 4rem; 
}

.footer__socials{
    display: flex;
    justify-content: center;
    gap: 1.5rem;
    margin-bottom: 2rem;
}

.footer__socials a{
    background: var(--color-bg);
    color: var(--color-white);
    padding: 0.8rem;
    border-radius: 0.7rem;
    display: flex;
    border:1px solid transparent;
}

.footer__socials a:hover{
    background: transparent;
    color: var(--color-white);
    border-color: var(--color-white);
}

.footer__copyright{
    margin-bottom:  4rem;
    color:var(--color-white)
}

/* ----------------- SMALL DEVICES ---------------- */
@media screen and (max-width: 600px){
    .permalinks{
        flex-direction: column;
        gap:1.5rem;
    }

    .footer__socials{
        margin-bottom: 2.5rem;
    }
}