header{
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}

.cta{
    margin-top: 2.5rem;
    display:flex;
    gap: 1.5rem;
    justify-content: center;
}

.me{
    background:linear-gradient(var(--color-primary),transparent);
    width: 22rem;
    height: 23rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0rem 0rem;
    overflow: hidden;
    padding: 3.1rem 1.5rem 1.5rem 1.5rem;
}



/* ----------------- MEDIUM DEVICES ---------------- */
@media screen and (max-width: 1024px){
    header{
        height:68vh;
    }
}

/* ----------------- SMALL DEVICES ---------------- */
@media screen and (max-width: 600px){
    header{
        height:100vh;
    }
    .header__socials,.scroll__down{
        display:none;
    }
}